import React from 'react';
import DatePicker from 'react-datepicker';
import '../DatePicker/DatePicker.css'
import 'react-datepicker/dist/react-datepicker.css';
import '../DatePicker/DatePicker.css';
import setHours from 'date-fns/setHours';
import setMinutes from 'date-fns/setMinutes';

class CustomDatePicker extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedDate: null,
      cur_MinTime_hr: 9,
      cur_MaxTime_hr: 18,
      cur_MinTime_min: 0,
      cur_MaxTime_min: 0,
      checkCurDate: true,
      styleStatus: true,
      excludeTimesMin: 1
    };
  }

  componentWillReceiveProps() {
    this.setState({
      selectedDate: null,
      cur_MinTime_hr: 9,
      cur_MaxTime_hr: 18,
      cur_MinTime_min: 0,
      cur_MaxTime_min: 0,
      styleStatus: true
    })
  }

  render() {
    var days = ['SUNDAY', 'MONDAY', 'TUESDAY', 'WEDNESDAY', 'THURSDAY', 'FRIDAY', 'SATURDAY'];
    var curDate = new Date();
    var dateStatus = true;
    const OnDateSelect = (date) => {
      if (Object.keys(this.props.departmentData).length > 0) {
        this.setState({ styleStatus: false })
        if (date.getDate() === curDate.getDate() && date.getFullYear() === curDate.getFullYear() && date.getMonth() === curDate.getMonth()) {
          this.setState({ checkCurDate: true }, () => dateStatus = true);
          dateStatus = true;
        }
        else {
          this.setState({ checkCurDate: false }, () => dateStatus = false);
          dateStatus = false;
        }

        this.setState({
          selectedDate: date,
          cur_MinTime_hr: dateStatus ? curDate.getHours() >= 18 ? 18 : curDate.getHours() : 9,
          cur_MaxTime_hr: 18,
          cur_MinTime_min: dateStatus ? curDate.getHours() >= 18 ? 0 : curDate.getMinutes() : 0,
          cur_MaxTime_min: 0,
          excludeTimesMin: dateStatus ? curDate.getHours() >= 18 ? 0 : 1 : 1
        })
        
        if (this.props.departmentData.weeklyAvailability && this.props.departmentData.weeklyAvailability.length > 0) {
          this.props.departmentData.weeklyAvailability.forEach(element => {
            if (element.dayOfWeek === days[date.getDay()]) {
              if (dateStatus) {
                this.setState({
                  cur_MinTime_hr: ((curDate.getHours() >= (element.startTime + "").slice(0, 2) && curDate.getHours() <= (element.endTime + "").slice(0, 2))) ? curDate.getHours() : (element.endTime + "").slice(0, 2),
                  cur_MinTime_min: ((curDate.getHours() >= (element.startTime + "").slice(0, 2) && curDate.getHours() <= (element.endTime + "").slice(0, 2))) ? curDate.getMinutes() : 0,
                  cur_MaxTime_hr: (element.endTime + "").slice(0, 2),
                  cur_MaxTime_min: (element.endTime + "").slice(3, 5),
                  excludeTimesMin: 0
                })
              }
              else {
                this.setState({
                  cur_MinTime_hr: (element.startTime + "").slice(0, 2),
                  cur_MinTime_min: (element.startTime + "").slice(3, 5),
                  cur_MaxTime_hr: (element.endTime + "").slice(0, 2),
                  cur_MaxTime_min: (element.endTime + "").slice(3, 5),
                })
              }
            }
          });
        }
      }
    };

    return (

      <div>
        {
          this.state.styleStatus ?
            <style dangerouslySetInnerHTML={{
              __html: `
            .react-datepicker__time-list-item--disabled{
              display: none;
            }
            .react-datepicker__time-list-item{
              color:#ccc;
            }    
            react-datepicker__time-list-item--selected{
              color:#ccc;
            }`}}></style>
            :
            <style dangerouslySetInnerHTML={{
              __html: `
            .react-datepicker__time-list-item--disabled{
              display: none;
            }
        `}}></style>
        }
        <DatePicker
          selected={this.state.selectedDate}
          onChange={date => OnDateSelect(date)}
          minDate={new Date()}
          showTimeSelect
          timeIntervals={this.props.departmentData.durationMins ? this.props.departmentData.durationMins : 15}
          useWeekdaysShort={true}
          timeCaption=""
          dateFormat="MMMM d, yyyy h:mm aa"
          minTime={setHours(setMinutes(new Date(), parseInt(this.state.cur_MinTime_min)), parseInt(this.state.cur_MinTime_hr))}
          maxTime={setHours(setMinutes(new Date(), parseInt(this.state.cur_MaxTime_min)), parseInt(this.state.cur_MaxTime_hr))}
          inline
          excludeTimes={[setHours(setMinutes(new Date(), this.state.excludeTimesMin), parseInt(this.state.cur_MaxTime_hr))]}
        />
      </div>
    );
  };
}

export default CustomDatePicker;
